<template>
	<div class="text-base h-screen">
		<div v-if="access.data?.access" class="mt-20 w-1/2 mx-auto">
			<div class="text-3xl font-bold">
				{{ __('Billing Details') }}
			</div>
			<div class="text-gray-600 mt-1">
				{{ __('Enter the billing information to complete the payment.') }}
			</div>
			<div class="border rounded-md p-8 mt-10">
				<div class="text-xl font-semibold">
					{{ __('Address') }}
				</div>
				<div class="text-gray-600 mt-1">
					{{ __('Specify your billing address correctly.') }}
				</div>
				<div class="grid grid-cols-2 gap-5 mt-4">
					<div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Billing Name') }}
							</div>
							<Input type="text" v-model="billingDetails.billing_name" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Address Line 1') }}
							</div>
							<Input type="text" v-model="billingDetails.address_line_1" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Address Line 2') }}
							</div>
							<Input type="text" v-model="billingDetails.address_line_2" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('City') }}
							</div>
							<Input type="text" v-model="billingDetails.city" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('State') }}
							</div>
							<Input type="text" v-model="billingDetails.state" />
						</div>
					</div>
					<div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Country') }}
							</div>
							<Link doctype="Country" v-model="billingDetails.country"/>
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Postal Code') }}
							</div>
							<Input type="text" v-model="billingDetails.pincode" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Phone Number') }}
							</div>
							<Input type="text" v-model="billingDetails.phone" />
						</div>
						<div class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Source') }}
							</div>
							<Link doctype="LMS Source" v-model="billingDetails.source"/>
						</div>
						<div v-if="billingDetails.country == 'India'" class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('GST Number') }}
							</div>
							<Input type="text" v-model="billingDetails.gstin" />
						</div>
						<div v-if="billingDetails.country == 'India'" class="mt-4">
							<div class="mb-1.5 text-sm text-gray-700">
								{{ __('Pan Number') }}
							</div>
							<Input type="text" v-model="billingDetails.pan" />
						</div>
					</div>
				</div>
				<Button variant="solid" class="mt-8" @click="generatePaymentLink()">
					{{ __('Proceed to Payment') }}
				</Button>
			</div>
		</div>
		<div v-else>
			<div class="text-base border rounded-md w-1/3 mx-auto my-32">
				<div class="border-b px-5 py-3 font-medium">
					<span
						class="inline-flex items-center before:bg-red-600 before:w-2 before:h-2 before:rounded-md before:mr-2"
					></span>
					{{ __('Not Permitted') }}
				</div>
				<div class="px-5 py-3">
					<div class="mb-4 leading-6">
						{{ access.data?.message }}
					</div>
					<Button
						v-if="!user.data.name"
						variant="solid"
						class="w-full"
						@click="redirectToLogin()"
					>
						{{ __('Login') }}
					</Button>
					<router-link
						v-else-if="type == 'course'"
						:to="{
							name: 'Courses',
						}"
					>
						<Button variant="solid">
							{{ __('Checkout Courses') }}
						</Button>
					</router-link>
					<router-link
						v-else-if="type == 'batch'"
						:to="{
							name: 'Batches',
						}"
					>
						<Button varian="solid">
							{{ __('Checkout Batches') }}
						</Button>
					</router-link>
				</div>
			</div>
		</div>
	</div>
</template>
<script setup>
import { Input, Button, createResource } from 'frappe-ui'
import { reactive, inject, onMounted, ref } from 'vue'
import Link from '@/components/Controls/Link.vue'
import { createToast } from '@/utils/'
import Razorpay from 'razorpay'

console.log(Razorpay);

const user = inject('$user')

const props = defineProps({
	type: {
		type: String,
		required: true,
	},
	name: {
		type: String,
		required: true,
	},
})

const access = createResource({
	url: 'lms.lms.api.validate_billing_access',
	params: {
		type: props.type,
		name: props.name,
	},
	auto: true,
})

const billingDetails = reactive({
	billing_name: '',
	address_line_1: '',
	address_line_2: '',
	city: '',
	state: '',
	pincode: '',
	country: '',
	phone: '',
	source: '',
	gstin: '',
	pan: '',
})

const redirectToLogin = () => {
	window.location.href = `/login?redirect-to=/billing/${props.type}/${props.name}`
}

const paymentOptions = createResource({
	url: 'lms.lms.utils.get_payment_options',
	makeParams(values) {
		return {
			doctype: props.type == 'course' ? 'LMS Course' : 'LMS Batch',
			docname: props.name,
			phone: billingDetails.phone,
			country: billingDetails.country
		}
	}
});

const generatePaymentLink = () => {
	paymentOptions.submit({}, {
		validate(params) {
			validateAddress()
		},
		onSuccess(data) {
			data.handler = (response) => {
				handleSuccess(response, doctype, docname, data.order_id);
			}
			let rzp1 = new Razorpay(data);
			rzp1.open();
		},
		onError(err) {
			createToast({
				title: 'Error',
				text: err.messages?.[0] || err,
				icon: 'x',
				iconClasses: 'bg-red-600 text-white rounded-md p-px',
				position: 'top-center',
				timeout: 10,
			})
		}
	})
}

const paymentResource = createResource({
	url: 'lms.lms.utils.verify_payment',
	makeParams(values) {
		return {
			response: values.response,
			doctype: props.type == 'course' ? 'LMS Course' : 'LMS Batch',
			docname: props.name,
			address: billingDetails,
			order_id: values.orderId,
		}
	}
})

const handleSuccess = (response, doctype, docname, orderId) => {
	paymentResource.submit({
		response: response,
		orderId: orderId
	}, {
		onSuccess(data) {
			createToast({
				title: 'Success',
				text: 'Payment Successful',
				icon: 'check',
				iconClasses: 'bg-green-600 text-white rounded-md p-px',
			})
			setTimeout(() => {
				window.location.href = data;
			}, 3000);
		}
	})
}

const validateAddress = () => {
	if (billingDetails.country == "India" && !billingDetails.state) {
		createToast({
			title: "Please enter a valid state with correct spelling and the first letter capitalized.",
			icon: "alert-circle",
			iconClasses: "text-yellow-600 bg-yellow-100",
		});
		return
	}

	const states = [
		"Andhra Pradesh",
		"Arunachal Pradesh",
		"Assam",
		"Bihar",
		"Chhattisgarh",
		"Goa",
		"Gujarat",
		"Haryana",
		"Himachal Pradesh",
		"Jharkhand",
		"Karnataka",
		"Kerala",
		"Madhya Pradesh",
		"Maharashtra",
		"Manipur",
		"Meghalaya",
		"Mizoram",
		"Nagaland",
		"Odisha",
		"Punjab",
		"Rajasthan",
		"Sikkim",
		"Tamil Nadu",
		"Telangana",
		"Tripura",
		"Uttar Pradesh",
		"Uttarakhand",
		"West Bengal",
	];
	if (
		billingDetails.country == "India" &&
		!states.includes(billingDetails.state)
	) {
		createToast({
			title: "Please enter a valid state with correct spelling and the first letter capitalized.",
			icon: "alert-circle",
			iconClasses: "text-yellow-600 bg-yellow-100",
		});
		return
	}
}
</script>